    function enableDrag(container) {
        console.log("   ");
        let target = null;
        let offsetX = 0, offsetY = 0;
        container.onmousedown = (e) => {
            const el = e.target.closest(".drag-box");
            if (!el)
                return;
            target = el;
            const rect = el.getBoundingClientRect();
            offsetX = e.clientX - rect.left;
            offsetY = e.clientY - rect.top;
            console.log(`  : ${el.textContent}`);
            document.onmousemove = onMove;
            document.onmouseup = onUp;
        };
        function onMove(e) {
            if (!target)
                return;
            const rect = container.getBoundingClientRect();
            let x = e.clientX - rect.left - offsetX;
            let y = e.clientY - rect.top - offsetY;
            if (x < 0)
                x = 0;
            if (y < 0)
                y = 0;
            if (x > rect.width - target.offsetWidth)
                x = rect.width - target.offsetWidth;
            if (y > rect.height - target.offsetHeight)
                y = rect.height - target.offsetHeight;
            target.style.left = `${x}px`;
            target.style.top = `${y}px`;
        }
        function onUp() {
            if (target) {
                console.log(`  :   = ${target.style.left}, ${target.style.top}`);
            }
            target = null;
            document.onmousemove = null;
            document.onmouseup = null;
        }
    }
