// ========================================================================================
//     (  )
// ========================================================================================
let isWorkAssignPanelInitialized = false;
export function initWorkAssignPanel(API_BASE) {
    const panel = document.getElementById("panel-");
    if (!panel)
        return;
    //     
    if (isWorkAssignPanelInitialized) {
        console.log(" initWorkAssignPanel      ");
        return;
    }
    isWorkAssignPanelInitialized = true;
    //    
    const employeeSelector = initEmployeeSelectFeature(panel, API_BASE);
    // DOM  
    const clientFilter = panel.querySelector("#assignClientFilter");
    const orderSelect = panel.querySelector("#assignOrderSelect");
    const categoryEl = panel.querySelector("#assignCategory");
    const instructionEl = panel.querySelector("#assignInstruction");
    const startDateEl = panel.querySelector("#assignStartDate");
    const endDateEl = panel.querySelector("#assignEndDate");
    const btnSave = panel.querySelector("#btnSaveAssignment");
    //    DOM
    const calMonthLabel = panel.querySelector("#calendarCurrentLabel");
    const calBody = panel.querySelector("#assignCalendarBody");
    const btnCalPrev = panel.querySelector("#calendarPrev");
    const btnCalNext = panel.querySelector("#calendarNext");
    let allOrders = [];
    function clearAllFields() {
        if (!panel)
            return;
        //  input 
        panel.querySelectorAll("input").forEach((el) => {
            el.value = "";
        });
        //  textarea 
        panel.querySelectorAll("textarea").forEach((el) => {
            el.value = "";
        });
        //  select 
        panel.querySelectorAll("select").forEach((el) => {
            el.selectedIndex = 0;
        });
        //   
        employeeSelector.clearAllEmployees();
    }
    // =========================================================================
    //  1.   
    // =========================================================================
    async function loadOrders() {
        try {
            const res = await fetch(`${API_BASE}/api/innomax-projects`);
            const json = await res.json();
            if (!json.ok)
                return;
            allOrders = json.rows;
        }
        catch (err) {
            console.error("   :", err);
        }
    }
    // =========================================================================
    //  2.        
    // =========================================================================
    clientFilter.addEventListener("change", () => {
        const cName = clientFilter.value.trim();
        orderSelect.innerHTML = `<option value=""> </option>`;
        if (!cName)
            return;
        const filtered = allOrders.filter((o) => o.detail_json.clientName === cName);
        filtered.forEach((o) => {
            const op = document.createElement("option");
            op.value = o.code_no;
            op.textContent = `${o.code_no}`;
            orderSelect.appendChild(op);
        });
        //     
        employeeSelector.clearAllEmployees();
    });
    // =========================================================================
    //  3.   (innomax_works)     
    // =========================================================================
    btnSave.addEventListener("click", async () => {
        const orderNo = orderSelect.value;
        const clientName = clientFilter.value;
        const employees = employeeSelector.getSelectedEmployees(); // [{id,name}, ...]
        const category = categoryEl.value;
        const instruction = instructionEl.value;
        const startDate = startDateEl.value;
        const endDate = endDateEl.value;
        if (!orderNo)
            return alert(" .");
        if (!category)
            return alert("  .");
        if (employees.length === 0)
            return alert(" .");
        if (!startDate || !endDate)
            return alert("  .");
        // ===============================================
        //   (progress_buffer)  
        // ===============================================
        //   
        function generateDateRange(start, end) {
            const result = [];
            const s = new Date(start);
            const e = new Date(end);
            const cur = new Date(s);
            while (cur <= e) {
                const y = cur.getFullYear();
                const m = String(cur.getMonth() + 1).padStart(2, "0");
                const d = String(cur.getDate()).padStart(2, "0");
                result.push(`${y}-${m}-${d}`);
                cur.setDate(cur.getDate() + 1);
            }
            return result;
        }
        const dateList = generateDateRange(startDate, endDate);
        //   
        const empNames = employees.map(e => e.id); // ex) ["",""]
        //  
        const progress_buffer = {};
        dateList.forEach(dateStr => {
            progress_buffer[dateStr] = {};
            empNames.forEach(name => {
                progress_buffer[dateStr][name] = {
                    status: null,
                    percent: null,
                    review: ""
                };
            });
        });
        // ===============================================
        //   payload
        // ===============================================
        const payload = {
            orderNo,
            clientName,
            employees,
            category,
            instruction,
            startDate,
            endDate,
            progress_buffer //   
        };
        try {
            const res = await fetch(`${API_BASE}/api/innomax-works`, {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(payload),
            });
            const json = await res.json();
            if (!json.ok) {
                console.error(json);
                return alert(" ");
            }
            alert("  !");
            // 
            await loadWorks();
            renderCalendar();
            clearAllFields();
        }
        catch (err) {
            console.error("   :", err);
            alert("   .");
        }
    });
    // ========================================================================================
    //    ( UI)  
    // ========================================================================================
    function initEmployeeSelectFeature(panel, API_BASE) {
        const employeeSelect = panel.querySelector("#assignEmployeeSelect");
        const btnAddEmployee = panel.querySelector("#btnAddEmployee");
        const employeeListContainer = panel.querySelector("#assignEmployeeList");
        const selectedText = panel.querySelector("#assignSelectedEmployees");
        const btnClearEmployee = panel.querySelector("#btnClearEmployeeSelection");
        //    
        const selectedEmployees = [];
        // ------------------------------------------------------------
        //     (/api/users)
        // ------------------------------------------------------------
        async function loadEmployees() {
            console.log(" loadEmployees() ");
            try {
                const url = `${API_BASE}/api/users`;
                const res = await fetch(url);
                const json = await res.json();
                const rows = Array.isArray(json) ? json : (json.rows ?? []);
                employeeSelect.innerHTML = `<option value=""> </option>`;
                rows.forEach((u) => {
                    const opt = document.createElement("option");
                    opt.value = u.Name; //  id  Name 
                    opt.textContent = u.Name; //   Name
                    employeeSelect.appendChild(opt);
                });
            }
            catch (err) {
                console.error("    :", err);
            }
        }
        // ------------------------------------------------------------
        //    UI 
        // ------------------------------------------------------------
        function refreshSelectedView() {
            employeeListContainer.innerHTML = "";
            selectedEmployees.forEach((emp) => {
                const chip = document.createElement("div");
                chip.className =
                    "flex items-center gap-1 px-2 py-1 rounded-full bg-indigo-50 border border-indigo-200 text-xs text-indigo-700";
                chip.setAttribute("data-id", emp.id);
                chip.innerHTML = `
                <span>${emp.name}</span>
                <button type="button"
                    class="ml-1 w-4 h-4 flex items-center justify-center rounded-full hover:bg-indigo-100 text-[10px] text-indigo-500 btn-remove-employee">
                    
                </button>
            `;
                employeeListContainer.appendChild(chip);
            });
            selectedText.textContent =
                selectedEmployees.length > 0
                    ? selectedEmployees.map((e) => e.name).join(", ")
                    : "";
        }
        // ------------------------------------------------------------
        //   
        // ------------------------------------------------------------
        btnAddEmployee.addEventListener("click", () => {
            const id = employeeSelect.value;
            const name = employeeSelect.options[employeeSelect.selectedIndex]?.text;
            if (!id)
                return alert(" .");
            if (selectedEmployees.some((e) => e.id === id)) {
                return alert("  .");
            }
            selectedEmployees.push({ id, name });
            refreshSelectedView();
        });
        // ------------------------------------------------------------
        //    (X )
        // ------------------------------------------------------------
        employeeListContainer.addEventListener("click", (e) => {
            const target = e.target;
            if (!target.classList.contains("btn-remove-employee"))
                return;
            const chip = target.closest("[data-id]");
            const removeId = chip.getAttribute("data-id");
            const idx = selectedEmployees.findIndex((e) => e.id === removeId);
            if (idx >= 0)
                selectedEmployees.splice(idx, 1);
            refreshSelectedView();
        });
        // ------------------------------------------------------------
        //   
        // ------------------------------------------------------------
        btnClearEmployee.addEventListener("click", () => {
            selectedEmployees.length = 0;
            refreshSelectedView();
        });
        //    
        loadEmployees();
        //     
        return {
            getSelectedEmployees: () => selectedEmployees,
            clearAllEmployees: () => {
                selectedEmployees.length = 0;
                refreshSelectedView();
            }
        };
    }
    //#region 
    // ======================================================
    //   
    // ======================================================
    let allWorks = [];
    let currentMonth = new Date();
    let tooltipEl = null;
    //   
    const employeeColorMap = {};
    const colorPalette = [
        "#A5B4FC", // indigo
        "#6EE7B7", // green
        "#FDE68A", // yellow
        "#FCA5A5", // red
        "#7DD3FC", // sky
        "#F9A8D4", // pink
        "#FDBA74" // orange
    ];
    function getColorForEmployee(name) {
        if (!name)
            return "#E5E7EB";
        if (!employeeColorMap[name]) {
            const keys = Object.keys(employeeColorMap);
            employeeColorMap[name] = colorPalette[keys.length % colorPalette.length];
        }
        return employeeColorMap[name];
    }
    // ======================================================
    //  Tooltip 
    // ======================================================
    function ensureTooltip() {
        if (!tooltipEl) {
            tooltipEl = document.createElement("div");
            tooltipEl.id = "assignCalendarTooltip";
            tooltipEl.className =
                "fixed z-50 max-w-xs px-2 py-1 text-[11px] rounded bg-gray-800 text-white shadow-lg pointer-events-none whitespace-pre-line";
            tooltipEl.style.display = "none";
            document.body.appendChild(tooltipEl);
        }
    }
    function showTooltip(text, x, y) {
        ensureTooltip();
        if (!tooltipEl)
            return;
        tooltipEl.textContent = text;
        tooltipEl.style.left = x + 12 + "px";
        tooltipEl.style.top = y + 12 + "px";
        tooltipEl.style.display = "block";
    }
    function hideTooltip() {
        if (tooltipEl)
            tooltipEl.style.display = "none";
    }
    // ======================================================
    //     Date 
    // ======================================================
    function parseDate(dateStr) {
        if (!dateStr)
            return null;
        const d = new Date(dateStr);
        if (isNaN(d.getTime()))
            return null;
        return new Date(d.getFullYear(), d.getMonth(), d.getDate());
    }
    // ======================================================
    //  innomax_works GET + detail_json 
    // ======================================================
    async function loadWorks() {
        try {
            console.log(" /api/innomax-works  ");
            const res = await fetch(`${API_BASE}/api/innomax-works`);
            console.log("  :", res.status);
            const json = await res.json();
            console.log("  JSON:", json);
            const rows = Array.isArray(json) ? json : (json.rows ?? []);
            // detail_json 
            allWorks = rows.map((row) => ({
                id: row.id,
                ...row.detail_json
            }));
            console.log("  allWorks:", allWorks);
        }
        catch (err) {
            console.error("   :", err);
        }
    }
    // ======================================================
    //    (GRID +  + )
    // ======================================================
    function renderCalendar() {
        if (!calBody || !calMonthLabel)
            return;
        const year = currentMonth.getFullYear();
        const month = currentMonth.getMonth();
        //   
        calMonthLabel.textContent = `${year} ${month + 1}`;
        //  grid 
        calBody.innerHTML = "";
        const firstDay = new Date(year, month, 1);
        const firstWeekday = firstDay.getDay();
        const daysInMonth = new Date(year, month + 1, 0).getDate();
        // 1)   
        for (let i = 0; i < firstWeekday; i++) {
            const blank = document.createElement("div");
            blank.className = "border bg-gray-50";
            calBody.appendChild(blank);
        }
        // 2)  
        for (let day = 1; day <= daysInMonth; day++) {
            const cellDate = new Date(year, month, day);
            const worksForDay = allWorks.filter((w) => {
                const s = parseDate(w.startDate);
                const e = parseDate(w.endDate) || s;
                if (!s)
                    return false;
                return (cellDate.getTime() >= s.getTime() &&
                    e &&
                    cellDate.getTime() <= e.getTime());
            });
            const cell = document.createElement("div");
            cell.className = "relative border p-1 text-[11px] bg-white flex flex-col";
            //  
            const header = document.createElement("div");
            header.className = "flex justify-between items-center mb-1";
            header.innerHTML = `
            <span class="font-semibold">${day}</span>
            ${worksForDay.length
                ? `<span class="text-[10px] text-indigo-600 font-medium">${worksForDay.length}</span>`
                : ""}
        `;
            cell.appendChild(header);
            // 
            if (worksForDay.length === 0) {
                const empty = document.createElement("div");
                empty.className = "text-[10px] text-gray-400";
                empty.textContent = " ";
                cell.appendChild(empty);
            }
            else {
                const list = document.createElement("div");
                list.className = "flex flex-col gap-1";
                worksForDay.forEach((w) => {
                    const employees = Array.isArray(w.employees) ? w.employees : [];
                    const item = document.createElement("div");
                    item.className =
                        "relative h-5 rounded-full overflow-hidden cursor-pointer";
                    // ============================
                    //        
                    // ============================
                    const barWrapper = document.createElement("div");
                    barWrapper.className = "absolute inset-0 flex";
                    employees.forEach((emp) => {
                        const empColor = getColorForEmployee(emp.name || emp.id);
                        const bar = document.createElement("div");
                        bar.style.backgroundColor = empColor;
                        bar.style.opacity = "0.6";
                        bar.className = "flex-1 h-full";
                        barWrapper.appendChild(bar);
                    });
                    //    
                    if (employees.length === 0) {
                        const bar = document.createElement("div");
                        bar.style.backgroundColor = "#E5E7EB";
                        bar.style.opacity = "0.5";
                        bar.className = "absolute inset-0";
                        barWrapper.appendChild(bar);
                    }
                    item.appendChild(barWrapper);
                    // ============================
                    //   
                    // ============================
                    const label = document.createElement("div");
                    label.className =
                        "relative z-10 text-[9px] px-1 truncate text-gray-800 font-medium";
                    label.textContent = `[${w.orderNo}] ${w.category}`;
                    item.appendChild(label);
                    // ============================
                    //  
                    // ============================
                    const empNames = employees.map((e) => e.name).join(", ");
                    const tooltip = `: ${w.orderNo} (${w.clientName})\n` +
                        `: ${w.category}\n` +
                        `: ${empNames || ""}\n` +
                        `: ${w.startDate} ~ ${w.endDate}\n` +
                        (w.instruction ? `: ${w.instruction}` : "");
                    item.addEventListener("mouseenter", (ev) => {
                        const e = ev;
                        showTooltip(tooltip, e.clientX, e.clientY);
                    });
                    item.addEventListener("mouseleave", hideTooltip);
                    list.appendChild(item);
                });
                cell.appendChild(list);
            }
            calBody.appendChild(cell);
        }
    }
    // ======================================================
    //    
    // ======================================================
    if (btnCalPrev) {
        btnCalPrev.addEventListener("click", () => {
            currentMonth = new Date(currentMonth.getFullYear(), currentMonth.getMonth() - 1, 1);
            renderCalendar();
        });
    }
    if (btnCalNext) {
        btnCalNext.addEventListener("click", () => {
            currentMonth = new Date(currentMonth.getFullYear(), currentMonth.getMonth() + 1, 1);
            renderCalendar();
        });
    }
    // ======================================================
    //   
    // ======================================================
    (async () => {
        ensureTooltip();
        await loadWorks();
        renderCalendar();
    })();
    //#endregion
    // =========================================================================
    //   
    // =========================================================================
    (async () => {
        await loadOrders();
        await loadWorks();
        renderCalendar();
        clearAllFields();
    })();
}
